@use "./commons" as *;
#wrap{
  padding: 20px 16px;
  background-color:rgba($white-color,0.5) ;
  area {
  cursor: pointer;
  pointer-events: auto;
  }
  header{
    position: relative;
    z-index: 999;
    .header_main{
      display: flex;
      padding-bottom: 23px;
      position: relative;
      .menu_icon {
        position: absolute;
        left: 0;
        .fa-bars{
          font-size: 28px;
          color: $black-color;
          cursor: pointer;
          transition: 0.3s;
          &:hover{
            opacity: 0.7;
            transform: scale(1.15);
          }
        }
      }
      h1{
        margin: 0 auto;
        a{
          display: inline-block;
          width: 160px;
          img{
            object-fit: cover;
            width: 100%;
          }
        }
      }
    }
    .header_menu{
      transition: margin 0.5s;
      margin-left: -100vw;
      position: fixed;
      top: 0;left: 0;
      background-image:linear-gradient(rgba($black-color,0.95),rgba($black-color,0.95)) ;
      width:100%;
      height: 100vh;
      .menu_inner{
        padding: 20px 16px;
        i{
          cursor: pointer;
          font-size: 28px;
          width: 100%;
          color: $white-color;
          position: absolute;
          right: 16px;
          opacity: 0.5;
          transition: 0.2s;
          &:hover{
            opacity: 1;
          }
        }
        h1{
          a{
            display: block;
            width: 170px;
            margin: 0 auto;
            margin-top: 22px;
            img{
              width: 100%;
              filter: invert(0.9);
              object-fit: cover;
            }
          }
        }
        nav{
          color: $white-color;
          margin-top: 44px;
          &>ul{
            &>li{
              &:hover>div>a,
              &:hover>div>i
              {
                color: $main-color;
              }
              &:first-child{
                margin-bottom: 40px;
              }
              &>div{
                display: flex;
                position: relative;
                align-items: center;
                & a:focus,
                & i:focus{
                  color: $main-color;
                } 
                i{
                  width: 100%;
                  text-align-last: left;
                  font-size: 20px;
                  display: inline-block;
                  line-height: 20px;
                  position: absolute;
                  top: 0;
                  left: 10px;
                  opacity: 1;
                }
                a{
                  transition:color 0.2s;
                  position: relative;
                  flex: 1;
                  display:inline-block;
                  font-size: $font-size-24px;
                  font-weight: 500;
                  padding-left: 40px;
                  padding-bottom: 14px;
                  border-bottom: 1px solid rgba($white-color,0.48);
                  margin-bottom: 6px;
                  &::before,
                  &::after{
                    content: "";
                    display: block;
                    width: 8px;height: 8px;
                    border-radius:50% ;
                    background-color: $white-color;
                    position: absolute;bottom:-4px;
                    filter:invert(0.48);
                  }
                  &::before{left: 0;}
                  &::after{right: 0;}
                }

              }
              ul{
                height: 0;
                overflow: hidden;
                transition: height 0.5s ease;
                li{
                  padding-left: 20px;
                  transition: 0.2s;
                  border-radius: 5px;
                  &:hover{
                    color: $main-color;
                    background-color:rgba($sub-color,0.08);
                  }
                  a{
                    display: block;
                    padding: 10px 0;
                    opacity: 0.8;
                    font-size: $font-size-20px; 
                    &:focus{
                      color: $main-color;
                      background-color:rgba($sub-color,0.08);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  main{
    margin-bottom: 80px;
    .custom-bnt {
      width: 150px;
      height: 40px;
      border-radius: 5px;
      font-family: sans-serif;
      font-weight: 500;
      color: $white-color;
      background: linear-gradient(0deg, rgba($main-color,1) 0%, rgba($main-color,0.8) 100%);
      display: inline-block;
      text-align: center;
      line-height: 40px;
      position: absolute;
      overflow: hidden;
      right: 20px;
      cursor: pointer;
      text-decoration: none;
      box-shadow:
        inset 2px 2px 2px rgba(255, 255, 255, 0.5),
        7px 7px 20px rgba(0, 0, 0, 0.1),
        4px 4px 5px rgba(0, 0, 0, 0.1);
      transition: all 0.3s ease;
      span {
        display: block;
        position: relative;
        z-index: 1;
        transition: color 0.3s ease;
        &::before,
        &::after {
          content: "";
          position: absolute;
          left: 0;
          bottom: 0;
          background:rgba($main-color,0.7);
          transition: all 0.3s ease;
        }
        &::before {
          width: 2px;
          height: 0%;
        }
        &::after {
          width: 0%;
          height: 2px;
        }
      }
      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        background:rgba($main-color,0.7);
        transition: all 0.3s ease;
      }
      &::before {
        height: 0%;
        width: 2px;
      }
      &::after {
        width: 0%;
        height: 2px;
      }
      &:hover {
        background: transparent;
        box-shadow: none;
        &::before {
          height: 100%;
        }
        &::after {
          width: 100%;
        }
        span {
          color: $main-color;
          &::before {
            height: 100%;
          }
          &::after {
            width: 100%;
          }
        }
      }
      &.bnt1{
        top: 120px;
      }
    }
    .year{
      font-size: $font-size-20px;
      display: inline-block;
      padding-bottom: 8px;
      padding-left: 10px;
      font-family: "Space Grotesk";
    }
    .main-title{
      font-family: "Space Grotesk";
      border-left: 4px solid $black-color;
      padding:5px 0 5px 6px;
      margin-bottom: 15px;
      letter-spacing: -1px;
      h2{
        font-size: $font-size-24px;
        margin-bottom: 4px;
        font-weight: 500;

      }
      p{
        font-family: "Noto Sans KR", sans-serif;
        font-size: $font-size-50px;
        font-weight: bold;
      }
    }
    & .tag{
      font-family: "Noto Sans KR", sans-serif;
      margin-bottom: 10px;
      display: inline-block;
      font-size: $font-size-16px;
      font-weight: 500;
      word-break: keep-all;
      padding: 10px;
      background-color:$back-color;
      border-radius: 5px;
      line-height: 1.5;
        word-spacing: 10px;
    }
    article{
      overflow: hidden;
      width: 100%;
      border-radius: 20px;
      &:nth-of-type(1){
        margin-bottom: 14px;
      }
      img{
        width: 100%; object-fit: cover;
      }
      map{
        background-color: darkblue;
        area{
          background-color: darkgoldenrod;
        }
      }
    }
  }
  @media (min-width:1200px) {
    &{
      width: 100%;
      margin-left: 380px;
      header{
        .header_main{
          display: none;
        }
        .header_menu{
          margin-left: 0vw;
          width: 380px;
          .menu_inner{
            &>i{display: none;}
            nav{
              .gnb{
                &>li{
                  &>div{
                    i{
                      font-size: $font-size-20px;
                    }
                    a{
                      font-size: $font-size-20px;
                    }
    
                  }
                  .sub{
                    li{
                      a{
                        font-size: $font-size-16px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      main{
        padding: 40px 30px 40px;
        .custom-bnt {
          width: 200px;
          height: 45px;
          line-height: 45px;
          right: 30px;
          &.bnt1{
            top: 184px;
          }
        }
        .main-title{
          padding-bottom: 5px;
          h2{font-size: $font-size-24px;}
        }
        & .tag{
          font-size: $font-size-14px;
          font-weight: 400;
        }
        article{
          margin-top: 10px;
          width: calc(100% - 390px);
        }
      }
    }
  }
}